body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body, html, #root, .App {
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 40px;
  padding: 20px;
  color: white;
}

.App-title {
  display: inline-block;
  float: left;
  margin: 0;
}

.App-header a {
  color: #aaa;
  display: inline-block;
  font-size: 18px;
  padding: 8px;
  margin-left: 12px;
  float: right;
  text-decoration: none;
}

.flexContainer {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  width: 100%;
}

.flexContainer .canvasContainer,
.flexContainer .continents {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.canvas, .canvasContainer {
  min-width: 300px;
  width: 50%;
  height: 100%;
}

.continents {
  width: 300px;
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
}

.continents div {
  padding: 16px;
}

.pair {
  display: inline-block;
}

.calendar {
  color: #000000;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  font-family: cursive;
}

.month {
  padding: 16px;
}

.calendar table .today {
  background-color: lightblue;
  border: 2px blue solid;

}

.calendar th {
  font-size: 12px;
  width: 50px;
}

.calendar td, .calendar table, .calendar .monthHeader {
  border: 1px black solid;
}

.calendar tbody tr {
  height: 50px;
}

.calendar thead tr {
  height: 32px;
}

.calendar table {
  border-collapse: collapse;
}

.calendar .monthHeader {
  font-size: 24px;
  padding: 12px;
  border-bottom: none;
}

.Tenebra .monthHeader {
  background-color: #434343;
  color: #ffffff;
}

.Tenebra table {
  background-color: #666666;
  color: #ffffff;
}

.Frigus .monthHeader, .Nix .monthHeader {
  background-color: #999999;
}

.Frigus table, .Nix table {
  background-color: #d9d9d9;
}

.Nascor .monthHeader {
  background-color: #6aa84f;
}

.Nascor table , .Pinnae table, .Serotine table {
  background-color: #93c47d;
}

.Pinnae .monthHeader, .Serotine .monthHeader {
  background-color: #38761d;
}

.Coctus .monthHeader {
  background-color: #ff0000;
}

.Coctus table,
.Crusta .monthHeader,
.Conlegi .monthHeader {
  background-color: #e69138;
}

.Crusta table,
.Conlegi table {
  background-color: #ffd966;
}

.Depostus .monthHeader {
  background-color: #e06666;
}

.Depostus table,
.Deliratio .monthHeader,
.Anima .monthHeader {
  background-color: #ea9999;
}

.Deliratio table,
.Anima table {
  background-color: #fff;
}

.hasLog {
	position: relative;
  cursor: pointer;
}

.hasLog:after { /* Magic Happens Here!!! */
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    display: block;
    border-left: 20px solid transparent;
    border-bottom: 20px solid transparent;
    border-top: 20px solid blue;
} /* </magic> */

.close {
  float: right;
  cursor: pointer;
  position: absolute;
  top: 0px;
  right: 0px;
  width: 36px;
  height: 32px;
  font-size: 24px;
}

.logContent {
  text-align: center;
}


.leaflet-container {
	height: 600px;
	width: 80%;
	margin: 0 auto;
}

